import React from 'react';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import * as yup from 'yup';
import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers';

import { SERVER_ADDRESS, post } from '../../../utils/remote';
import { setToken } from '../../../utils/auth';
import { dispatch } from '../../../utils/redux';
import jwt from 'jsonwebtoken';
import { useHistory } from 'react-router-dom';
import { createUserAction } from '../../../utils/redux/user';
import { makeStyles } from '@material-ui/styles';
import { Card, CardContent } from '@material-ui/core';

const schema = yup.object().shape({
  email: yup.string().email().required(),
  password: yup.string().max(122).required(),
});

const useStyles = makeStyles({
  root: {
    maxWidth: '300px',
    margin: '0 auto',
  }
})

export function SessionCreate() {
  const classes = useStyles();
  const history = useHistory();
  const { handleSubmit, register, errors } = useForm({
    resolver: yupResolver(schema),
  });

  const onSubmit = (data: any) => {
    post(SERVER_ADDRESS+'/sessions', {
      body:
        {
          email: data.email,
          password: data.password,
        }
    }).then((res)=>{
      setToken(res.data);
      dispatch(createUserAction(res.data));
      history.push('/');
    });
  }
  return (
    <div>
      <Card className={classes.root}>
        <CardContent>
        <Typography variant="h6" component="h1">Login</Typography>
        <form
          onSubmit={handleSubmit(onSubmit)}
        >
          <div>
            <TextField
              name="email"
              inputRef={register}
              label="Email"
              error={!!errors.email}
              helperText={errors.email?.message}
              fullWidth
            />
          </div>
          <div>
            <TextField 
              inputRef={register}
              name="password"
              label="Password"
              type="password"
              error={!!errors.password}
              helperText={errors.password?.message}
              fullWidth
            />
          </div>
          <Button type="submit">Login</Button>
        </form>
        </CardContent>
      </Card>
    </div>
  );
}
