import React, { useEffect, useState } from 'react';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { v4 } from 'uuid';
import * as yup from 'yup';
import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers';

import { SERVER_ADDRESS, post } from '../../../utils/remote';
import { setToken } from '../../../utils/auth';

import { useLocation } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { createUserAction } from '../../../utils/redux/user';
import { makeStyles } from '@material-ui/styles';
import { Card, CardActions, CardContent, CardHeader, Divider } from '@material-ui/core';
import qs from 'query-string';
import { setupIntents } from '../../../utils/stripe';
import { Box } from '@material-ui/core';
export const endpoint = SERVER_ADDRESS+'/privateUsers';

const useStyles = makeStyles({
  root: {
    maxWidth: '300px',
    margin: '0 auto',
  },
})

export function UserCreate() {
  const classes = useStyles();
  const [id] = useState(v4())
  return (
    <div>
      <Card className={classes.root}>
        <CardHeader title="Signup"/>
          <UserCreateForm
            key={id}
            id={id}
          />
      </Card>
    </div>
  );
}

const schema = yup.object().shape({
  // username: yup.string().matches(/^[a-z0-9]+$/i, 'Only alphanumeric characters').required(),
  email: yup.string().email().required(),
  password: yup.string().min(6).max(122).required(),
  referralCode: yup.string().optional(),
});



const UserCreateForm = ({id}: {id: string})=>{
  const location = useLocation();
  const parsed = qs.parse(location.search);
  const { handleSubmit, register, errors } = useForm({
    resolver: yupResolver(schema),
  });
  const [apiErrors, setApiErrors] = useState<string>("");
  const [referralCodeError, setReferralCodeError] = useState<string>("");
  
  const dispatch = useDispatch();
  useEffect(()=>{
    if(parsed.referral_code){
      yup.string().uuid()
      .isValid(parsed.referral_code).then((valid)=>{
        if(!valid){
          setReferralCodeError("Bad referral code. You may continue to signup, but the referral will not be counted towards possible credits")
        }
      })
    }
  }, [])
  const validReferralCode = referralCodeError ? "" : (parsed.referral_code || '');
  const onSubmit = async (data: any) => {    
    post(endpoint, {
      body:
        {
          id: v4(),
          // username: data.username,
          email: data.email,
          password: data.password,
          referralCode: data.referralCode
        }
    }).then(async (res)=>{
      setToken(res.data);
      dispatch(createUserAction(res.data));
      setupIntents()
    })
    .catch((error)=>{
      error.json().then((err: any) => {
        setApiErrors(err.message);
      });
    });
  }
  const errorExists = referralCodeError || apiErrors;
  return <form
      onSubmit={handleSubmit(onSubmit)}
    >
    <CardContent>
      {
        !errorExists ? null : <Typography variant="body2" color="secondary">{errorExists}</Typography>
      }
      <TextField
        id="email"
        name="email"
        inputRef={register}
        label="Email"
        fullWidth
        error={!!errors.email}
        helperText={errors.email?.message}
      />
      <TextField 
        fullWidth
        inputRef={register}
        id="password"
        name="password"
        label="Password"
        type="password"
        error={!!errors.password}
        helperText={errors.password?.message}
      />
      <input ref={register} name="referralCode" hidden value={validReferralCode} />
    </CardContent>
    <CardContent>
      {
          <Typography variant="body1"><Box fontWeight="fontWeightBold" m="1">{`${validReferralCode ? 'First two months are free' : 'First month is free'}`}</Box></Typography>
      }
      <Divider/>
      <Typography variant="body2">Monthly price is $2.00(USD)/month</Typography>
      <Typography variant="body2">Cancel at anytime</Typography>
    </CardContent>
    <CardActions>
      <Button type="submit">Signup</Button>
    </CardActions>
  </form>
}