import React, { cloneElement } from 'react';
import { Snackbar, IconButton } from '@material-ui/core';
import { Close as CloseIcon } from '@material-ui/icons';

import { UserCreate } from '../../../users/Create';
import { Switch, Route, useHistory, useLocation } from 'react-router-dom';
import { SessionCreate } from '../../../sessions/Create';
import SearchActivities from '../../../pages/Search';
import Dashboard from '../../../pages/Dashboard';
import ActivityShow from '../../../shedules/activities/Show';


import { useSelector, useDispatch } from 'react-redux';
import { v4 } from 'uuid';
import ShedulesAddedActivitiesShowMany from '../../../shedules/activities/added/ShowMany';
import Contact from '../../../pages/Contact';
import { userSelector } from '../../../../utils/redux/user';
import { createGlobalAction, globalSelector } from '../../../../utils/redux/global';
import UserCreateError from '../../../users/Create/Error';
import UserCreateSuccess from '../../../users/Create/Success';



const SHEDULES = '/shedules';

export default function MainRoutes() {
  const dispatch = useDispatch();
  const {id, message} = useSelector(globalSelector).snackbar
  const handleClose = ()=>dispatch({
    snackbar: ''
  });
  return <>
    <Snackbar
      key={id}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'left',
      }}
      open={!!id}
      autoHideDuration={6000}
      onClose={handleClose}
      action={
        <IconButton size="small" aria-label="close" color="inherit" onClick={handleClose}>
          <CloseIcon fontSize="small" />
        </IconButton>
      }
      message={message}
    />
  <Switch>
    {
      <Route exact path="/search"><RouteWrapper mustBeLoggedIn><SearchActivities /></RouteWrapper></Route>
    }
    
    <Route exact path="/contact"><Contact/></Route>
    <Route exact path="/"><RouteWrapper mustBeLoggedIn><Dashboard /></RouteWrapper></Route>
    {
      // <Route exact path="/config"><MustBeLoggedIn><UsersEdit /></MustBeLoggedIn></Route>
    }
    <Route exact path={`/activities/added`}><RouteWrapper mustBeLoggedIn><ShedulesAddedActivitiesShowMany /></RouteWrapper></Route>
    <Route exact path={`/activities/added`}><RouteWrapper mustBeLoggedIn ><ShedulesAddedActivitiesShowMany /></RouteWrapper></Route>
    <Route exact path={`${SHEDULES}/activities/:sheduleId`}><RouteWrapper mustBeLoggedIn><ActivityShow /></RouteWrapper></Route>
    <Route exact path={`/signup`}><UserCreate/></Route>
    <Route exact path={`/login`}><SessionCreate/></Route>
    <Route exact path={`/error`}><RouteWrapper mustBeLoggedIn><UserCreateError /></RouteWrapper></Route>
    <Route exact path={`/success`}><RouteWrapper mustBeLoggedIn><UserCreateSuccess /></RouteWrapper></Route>
  </Switch>
  </>
};


function RouteWrapper({children, mustBeLoggedIn, ...props}: {children: JSX.Element, mustBeLoggedIn?: Boolean}){
  const history = useHistory();
  const location = useLocation();
  const user = useSelector(userSelector);
  if(mustBeLoggedIn){
    if(!user){
      history.push('/overview')
      return null;
    }
    if(!user.creditApproved){
      if(location.pathname !== '/error' && location.pathname !== '/success'){
        history.push('/error')
        return null
      }

    
    
    }
  }  
  return <>    
    {
      cloneElement(children, props)
    }
  </>
}