import React, { useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { SERVER_ADDRESS, useCustomSwr } from '../../../../utils/remote';
import { useDispatch, useSelector } from 'react-redux';
import { createUserAction, userSelector } from '../../../../utils/redux/user';

export default function CreateUserSuccess(){
  const history = useHistory();
  const { data } = useCustomSwr(SERVER_ADDRESS+'/users/payments/intents')
  const user = useSelector(userSelector);
  const dispatch = useDispatch()
  useEffect(()=>{
    if(data){
      dispatch(createUserAction(data.data))
    }
  }, [data])
  useEffect(()=>{
    if(user.creditApproved){
      history.push('/')
    }
  }, [user])
  return null
}